import { Warranty } from "@/services/api/types";
// ุฏุงุฏูโูุง ููุชุฑ ูุถุนุช ูุฎุชุต ฺฏุงุฑุงูุช
const warrantyStatusFilters: SelectOption[] = [
    { value: 'all', label: 'ููู ูุถุนุชโูุง' },
    { value: 'true', label: 'ูุนุงู' },
    { value: 'false', label: 'ุบุฑูุนุงู' },
];

export default function TicketPage() {
  const pathname = usePathname();
  const router = useRouter();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  // ุชุงุจุน ูุงฺฉุด ุฏุงุฏู ุฑุง ุจุง useCallback ูพุงุฏุงุฑ ูโฺฉูู
  const fetchWarrantiesData = useCallback((page: number, size: number, filters?: any) => {
    // ุฏุฑ ุขูุฏู ูโุชูุงูุฏ ููุชุฑูุง ุฑุง ุจู API ูพุงุณ ุฏูุฏ
    return getWarranties(page, size);
  }, []);

  // ูุฑุงุฎูุงู ููฺฉ ููุดููุฏ ุจุง ูพฺฉุฑุจูุฏ ฺฉุงูู
  const {
    data: warranties,
    totalCount,
    isLoading,
    error,
    currentPage,
    pageSize,
    searchTerm,
    activeTimeFilter,
    selectedStatus,
    handlePageChange,
    handlePageSizeChange,
    handleSearchChange,
    handleTimeFilterChange,
    handleStatusChange,
    // ูุง ุจู ููฺฉ ูโฺฏูู: "ุชุงุจุน refreshData ุฑุง ุจฺฏุฑ ู ุขู ุฑุง ุฏุฑ ูุชุบุฑ ุจู ูุงู refreshWarranties ุฐุฎุฑู ฺฉู"
    refreshData: refreshWarranties, 
  } = useListPage<Warranty>({
    fetchData: fetchWarrantiesData,
    searchKeys: ['title'],
    dateKey: 'createDate',
    statusKey: 'isActive',
  });
  
  // State ูุง ูุฎุชุต ููุฏุงู ู ุงฺฉุดูโูุง ุงู ุตูุญู
 const [isFormModalOpen, setIsFormModalOpen] = useState(false);
  const [editingWarranty, setEditingWarranty] = useState<Warranty | null>(null);
  
  // ฒ. State ูุง ุฌุฏุฏ ุจุฑุง ููุฏูุง ูุฑู
  const [formTitle, setFormTitle] = useState('');
  const [formIsActive, setFormIsActive] = useState(true); // ููุฏุงุฑ ูพุดโูุฑุถ ุจุฑุง ุงูุฒูุฏู

  const [actionLoading, setActionLoading] = useState(false);
  const [isExporting, setIsExporting] = useState(false);

  // --- Handlers ---
  const handleOpenAddModal = () => {
    setEditingWarranty(null);
    setFormTitle('');
    setFormIsActive(true); // ุฑุณุช ฺฉุฑุฏู ุจู ุญุงูุช ูพุดโูุฑุถ
    setIsFormModalOpen(true);
  };

  const handleOpenEditModal = (warranty: Warranty) => {
    setEditingWarranty(warranty);
    setFormTitle(warranty.title);
    setFormIsActive(warranty.isActive); // ููุฏุงุฑุฏู ุงููู ุงุฒ ุขุชู ุฏุฑ ุญุงู ูุฑุงุด
    setIsFormModalOpen(true);
  };

  const handleFormSubmit = async () => {
    if (!formTitle.trim()) {
      alert("ุนููุงู ฺฏุงุฑุงูุช ููโุชูุงูุฏ ุฎุงู ุจุงุดุฏ.");
      return;
    }
    setActionLoading(true);
    try {
      if (editingWarranty) {
        // ณ. ุฏุฑ ุญุงูุช ูุฑุงุดุ isActive ุฑุง ูู ุงุฑุณุงู ูโฺฉูู
        await updateWarranty(editingWarranty.id, { 
          title: formTitle, 
          isActive: formIsActive 
        });
      } else {
        // API ุดูุง ุจุฑุง createุ ููุฏ isActive ุฑุง ููโูพุฐุฑุฏุ ูพุณ ููุท title ุฑุง ูโูุฑุณุชู
        // ุงฺฏุฑ API ุชุบุฑ ฺฉุฑุฏุ ูโุชูุงูุฏ isActive ุฑุง ูู ุงูุฌุง ุงุถุงูู ฺฉูุฏ
        await createWarranty(formTitle);
      }
      setIsFormModalOpen(false);
      await refreshWarranties();
    } catch (err) {
      alert("ุฎุทุง ุฏุฑ ุฐุฎุฑูโุณุงุฒ ฺฏุงุฑุงูุช.");
    } finally {
      setActionLoading(false);
    }
  };


  const handleViewWarranty = (warrantyId: number) => {
    // ุดูุง ูโุชูุงูุฏ ุจุฑุง ูุดุงูุฏู ฺฉ ุตูุญู ุฌุฏุงฺฏุงูู ุจุณุงุฒุฏ ุง ุงุฒ ููุงู ุตูุญู ูุฑุงุด ุงุณุชูุงุฏู ฺฉูุฏ
    router.push(`/warranty/${warrantyId}`);
  };

  const handleDelete = async (warrantyId: number) => {
    if (window.confirm("ุขุง ุงุฒ ุญุฐู ุงู ฺฏุงุฑุงูุช ูุทูุฆู ูุณุชุฏุ")) {
      try {
        await deleteWarranty(warrantyId);
        await refreshWarranties(); 
        
      } catch (err) {
        alert("ุฎุทุง ุฏุฑ ุญุฐู ฺฏุงุฑุงูุช.");
      }
    }
  };

  const handleExport = async () => {
    // ููฺฉ ูุง ุฏุฑ ุญุงู ุญุงุถุฑ ููุท ุฏุงุฏูโูุง ุตูุญูโุจูุฏ ุดุฏู ุฑุง ุจุฑูโฺฏุฑุฏุงูุฏ.
    // ุจุฑุง ุฎุฑูุฌ ฺฉุงููุ ุจุงุฏ ููฺฉ ุฑุง ุงุฑุชูุง ุฏูู ุชุง ฺฉู ูุณุช ููุชุฑ ุดุฏู ุฑุง ุจุฑฺฏุฑุฏุงูุฏ.
    // ูุนูุงู ุงุฒ ุฏุงุฏูโูุง ุตูุญู ูุนู ุฎุฑูุฌ ูโฺฏุฑู.
    if (!warranties.length) {
      alert("ูฺ ุฏุงุฏูโุง ุจุฑุง ุฎุฑูุฌ ฺฏุฑูุชู ูุฌูุฏ ูุฏุงุฑุฏ.");
      return;
    }
    setIsExporting(true);
    await new Promise(resolve => setTimeout(resolve, 500));
    const dataToExport = warranties.map(w => ({
      'ุดูุงุณู': w.id,
      'ุนููุงู': w.title,
      'ูุถุนุช': w.isActive ? "ูุนุงู" : "ุบุฑูุนุงู",
      'ุชุงุฑุฎ ุซุจุช': new Date(w.createDate).toLocaleDateString('fa-IR'),
    }));
    exportToExcel(dataToExport, "ูุณุช_ฺฏุงุฑุงูุชโูุง");
    setIsExporting(false);
  };

  // --- Column Definitions ---
 const warrantyColumns: ColumnDefinition<Warranty>[] = [
    {
      // ณ. โจ ฺฉ ุณุชูู ุฌุฏุฏ ุจุฑุง "ุฑุฏู"
      accessorKey: 'id', // ูโุชูุงูู ุงุฒ id ุงุณุชูุงุฏู ฺฉูู ฺูู ููุญุตุฑ ุจู ูุฑุฏ ุงุณุช
      header: 'ุฑุฏู',
      // ด. โจ ุงุฒ index ุจุฑุง ูุญุงุณุจู ุดูุงุฑู ุฑุฏู ุงุณุชูุงุฏู ูโฺฉูู
      cell: (warranty, index) => {
        const rowNumber = (currentPage - 1) * pageSize + index + 1;
        return <Label text={String(rowNumber)} size="md" />;
      },
    },
    { accessorKey: 'title', header: 'ุนููุงู ฺฏุงุฑุงูุช', cell: (w) => <Label text={w.title} size="md" /> },
    { accessorKey: 'isActive', header: 'ูุถุนุช', cell: (w) => <Badge label={w.isActive ? "ูุนุงู" : "ุบุฑูุนุงู"} variant={w.isActive ? "success" : "neutral"} /> },
    { accessorKey: 'createDate', header: 'ุชุงุฑุฎ ุซุจุช', cell: (w) => <Label text={new Date(w.createDate).toLocaleDateString('fa-IR')} size="md" /> },
  ];
  
  // --- Props Preparation ---
  const filterSection = (
    <div style={{ display: 'flex', flexWrap: 'wrap', gap: '1rem', alignItems: 'center' }}>
      <FilterTabs
        tabs={commonTimeFilters}
        activeTab={activeTimeFilter}
        onTabChange={handleTimeFilterChange}
      />
      <Select
        options={warrantyStatusFilters}
        value={String(selectedStatus)}
        onChange={handleStatusChange}
        aria-label="ููุชุฑ ุจุฑ ุงุณุงุณ ูุถุนุช"
      />
    </div>
  );
  
  const listPageProps = {
    listPageHeaderProps: {
      title: "ูุฏุฑุช ฺฏุงุฑุงูุชโูุง",
      totalItems: totalCount,
      searchInputProps: {
        value: searchTerm,
        onChange: handleSearchChange,
        placeholder: "ุฌุณุชุฌู ุฏุฑ ุนูุงูู...",
      },
      exportButtonProps: { onClick: handleExport, isLoading: isExporting },
      actions: <Button onClick={handleOpenAddModal}>ุงูุฒูุฏู ฺฏุงุฑุงูุช</Button> 
    },
    tableProps: {
      columns: warrantyColumns,
      data: warranties,
      emptyStateMessage: "ูฺ ฺฏุงุฑุงูุช ุงูุช ูุดุฏ.",
      rowActions: (warranty: Warranty) => ({
      onView: () => handleViewWarranty(warranty.id), // <-- ุงู ุฎุท ุงุถุงูู ุดุฏ
      onEdit: () => handleOpenEditModal(warranty),
      onDelete: () => handleDelete(warranty.id),
    }),
    },
    paginationProps: {
      currentPage: currentPage,
      pageSize: pageSize,
      totalCount: totalCount,
      onPageChange: handlePageChange,
      pageSizeOptions: pageSizeOptions,
      onPageSizeChange: handlePageSizeChange,
    },
    filterSection: filterSection,
    isLoading: isLoading,
    error: error,
  };

  // --- JSX ---
  return (
    <>
      <YadakchiAdminLayout
        currentPath={pathname}
        renderLink={renderLink}
      >
        <ListPageLayout {...listPageProps} />
      </YadakchiAdminLayout>

      <FormModal
        isOpen={isFormModalOpen}
        onClose={() => setIsFormModalOpen(false)}
        title={editingWarranty ? "ูุฑุงุด ฺฏุงุฑุงูุช" : "ุงูุฒูุฏู ฺฏุงุฑุงูุช ุฌุฏุฏ"}
        onConfirm={handleFormSubmit}
        isLoading={actionLoading}
        confirmText={editingWarranty ? "ุฐุฎุฑู ุชุบุฑุงุช" : "ุงุฌุงุฏ ฺฏุงุฑุงูุช"}
      >
        <form 
          onSubmit={(e) => { e.preventDefault(); handleFormSubmit(); }}
          style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}
        >
          <Input
            id="warranty-title"
            label="ุนููุงู ฺฏุงุฑุงูุช"
            value={formTitle}
            onChange={(e) => setFormTitle(e.target.value)}
            required
            autoFocus
          />
          {/* ููุท ุฏุฑ ุญุงูุช ูุฑุงุดุ ToggleSwitch ุฑุง ููุงุด ูโุฏูู (ฺูู API create ุขู ุฑุง ููโูพุฐุฑุฏ) */}
          {editingWarranty && (
            <ToggleSwitch
              label="ฺฏุงุฑุงูุช ูุนุงู ุจุงุดุฏ"
              enabled={formIsActive}
              onChange={setFormIsActive}
            />
          )}
        </form>
      </FormModal>
    </>
  );
}